* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
  margin: 0;
  padding: 0;
}

html {
  color-scheme: light dark;
  font-family: Arial, Helvetica, sans-serif;
}

#debug {
  z-index: 9999;
  color: #fff;
  background-color: #000000b3;
  max-width: 300px;
  max-height: 150px;
  padding: 4px 8px;
  font-size: 12px;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.hide {
  display: none;
}

.app {
  cursor: default;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
}

.screen-wrapper {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.screen {
  user-select: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.title-screen {
  z-index: 1;
  color: #333;
  text-align: center;
  background-color: #666;
  font-size: 16px;

  & .title-content {
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    max-height: 700px;
    padding: 40px 0 100px;
    display: flex;

    & .title-image {
      margin-bottom: 30px;
    }

    & .title-angel-bob {
      will-change: transform;
      align-self: self-start;
      animation: 2s ease-in-out infinite angel-bob;
      display: inline-block;
    }

    & .title-angel {
      transform-origin: center;
      will-change: transform;
      width: 80%;
      animation: 6s linear infinite angel-walk;
      display: inline-block;
    }

    & .interaction-area {
      flex-direction: row;
      justify-content: right;
      width: 100%;
      display: flex;

      & .start-button {
        cursor: pointer;
        will-change: transform;
        background-color: #0000;
        border: none;

        &:active {
          transform: translate(2px, 2px);
        }
      }
    }

    & .progress-bar {
      background-color: #fefefe;
      border: 4px solid #222;
      border-radius: 8px;
      max-width: 250px;
      height: 18px;

      &::part(bar) {
        background-color: #ed6e68;
        border-radius: 8px;
        transition: width 1.2s;
      }
    }
  }
}

snow-overlay {
  position: relative;

  & .witch {
    z-index: 2;
    user-select: none;
    pointer-events: none;
    animation: 2s linear infinite angel-bob;
    display: block;
    position: absolute;
    top: 362px;
    left: 1228px;
  }
}

.calendar-screen {
  z-index: 0;
}

.pan-container {
  flex: 1 1 0;
  width: 100%;
  height: 100%;
}

blur-overlay {
  --width: 15px;
  --edge-opacity: .4;
}

audio-toggle {
  position: fixed;
  bottom: 20px;
  right: 80px;

  &:active {
    transform: scale(.9);
    box-shadow: 0 1px 3px #00000059;
  }
}

fullscreen-toggle {
  position: fixed;
  bottom: 20px;
  right: 20px;

  &:active {
    transform: scale(.9);
    box-shadow: 0 1px 3px #00000059;
  }
}

@keyframes angel-walk {
  0% {
    transform: translateX(0)scaleX(1);
  }

  49.999% {
    transform: translateX(120px)scaleX(1);
  }

  50% {
    transform: translateX(120px)scaleX(-1);
  }

  99.999% {
    transform: translateX(0)scaleX(-1);
  }

  100% {
    transform: translateX(0)scaleX(1);
  }
}

@keyframes angel-bob {
  0% {
    transform: translateY(0);
  }

  25% {
    transform: translateY(-2px);
  }

  50% {
    transform: translateY(0);
  }

  75% {
    transform: translateY(2px);
  }

  100% {
    transform: translateY(0);
  }
}
